import React, { Component } from 'react';

class Tag extends Component {

  constructor(props) {
    super(props);
    this.state = {
      isFocused: false,
      touched: false,
    };
    this.handleChange = this.handleChange.bind(this);
    this.makeClassName = this.makeClassName.bind(this);
  }

  handleChange(value) {
    this.props.onValueUpdate({
      [this.props.inputName]: {
        readOnly: this.props.readOnly,
        value: value
      }
    });
  }
  makeClassName() {
    return 'tags'
      + (this.props.validator(this.props.value) ? ' valid' : ' invalid')
      + (this.state.touched ? ' touched' : ' untouched')
      + (this.state.isFocused ? ' focused' : '');
  }
  handleAdd(value) {
    if (value !== '') {
      const newValue = this.props.value.concat([value]);
      this.handleChange(newValue);
    }
  }
  removeTag(value) {
    const index = this.props.value.indexOf(value);
    if (index !== -1) {
      let newValue = this.props.value;
      newValue.splice(index, 1);
      this.handleChange(newValue);
    }
  }
  render() {
    const allTags = this.props.value.map((tag, index) => (
      <li className="tag" key={index}>
        <span className="close" onClick={
          () => {this.removeTag(tag);}
        }>&times;</span>
        {tag}
      </li>
    ));
    return (
      <div className={this.makeClassName()}>
        <label htmlFor={this.props.inputName}>
          {this.props.label}
        </label>
        <ul>
          {allTags}
        </ul>
        <input type="text"
               className="tag-typer"
               placeholder={this.props.placeholder}
               onFocus={() => {
                 this.setState({
                   touched: true,
                   isFocused: true
                 });
               }}
               onKeyDown={(e) => {
                 if (e.keyCode === 13) {
                   e.preventDefault();
                   this.handleAdd(e.target.value);
                   e.target.value = '';
                 }
               }}
               onBlur={(e) => {
                 this.handleAdd(e.target.value);
                 this.setState({
                   isFocused: false
                 });
                 e.target.value = '';
               }}
               name={this.props.inputName}
        />
      </div>
    );
  }
}

Tag.propTypes = {
  type: React.PropTypes.string,
  input: React.PropTypes.string,
  inputName: React.PropTypes.string,
  placeholder: React.PropTypes.string,
  value: React.PropTypes.array,
  label: React.PropTypes.string,
  validator: React.PropTypes.func,
  onValueUpdate: React.PropTypes.func,
  readOnly: React.PropTypes.bool,
};

export default Tag;



